
@import "./../../base/variables/vars";

.user-bg {
  position: absolute;
  top: 0;
  z-index: 0;
  width: 100%;
  height: 226px;
  background: url(./../../../imgs/user-bg.png) no-repeat;
  background-size: cover;
}

.user {
  position: relative;
  z-index: 1;
  margin-top: 80px;
}

.user-head-sign {
  width: 70px;
  height: 70px;
  border: 2px solid #fff;
  border-radius: 50%;
}

.user-head {
  padding: 0 44px 20px;

  .user-head-sign {
    width: 60px;
    height: 60px;
  }

  .user-head-name,
  .user-head-small {
    padding: 0 4px 4px;
    font-size: 20px;
    color: #fff;
  }

  .user-head-name {
    font-size: 20px;
  }

  .user-head-small {
    font-size: 14px;
  }

  .user-head-arrow {
    position: relative;

    .icon {
      position: absolute;
      top: 50%;
      right: -20px;
      color: #fff;
      transform: translateY(-50%);
    }
  }
}

.user-main {
  padding: 0 20px 20px;
  margin-top: 10px;
}

.user-status {
  padding: 18px 20px;
  color: @gray-darker;
  text-align: center;

  .icon {
    font-size: 36px;
  }

  .user-status-label {
    margin: 4px 0;
    font-size: 18px;
  }

  .user-status-value {
    font-size: 12px;

    & > div {
      height: 24px;
    }
  }
}

.user-cell {
  overflow: hidden;
  border-radius: 4px;
}
