@import "./../base/variables/vars";

.login-bg {
  background: url(./../../../assets/imgs/login-bg.png) no-repeat top center;
  background-size: contain;
}

.login-form {
  // height:341px;
  padding: 20px 10px;
  background: rgba(255, 255, 255, 1);
  border-radius: 12px;
  box-shadow: 0 3px 12px 0 rgba(223, 223, 223, 0.9);

  .van-field__label {
    width: 50px;
    color: @gray-light;
  }
}

.reg {
  .van-field__label {
    width: 70px;
  }
}

.login-tab {
  margin-bottom: 20px;

  .login-tab-item {
    position: relative;
    padding: 0 0 12px;
    font-size: 12px;
    color: @gray-light;
    text-align: center;

    &:after {
      position: absolute;
      bottom: 0;
      left: 50%;
      width: 80%;
      height: 2px;
      background-color: @green;
      content: '';
      transform: translateX(-50%);
    }
  }
}

.login {
  padding: 0 30px;

  .logo {
    margin-bottom: 24px;
    text-align: center;
  }
}

.agreement-popup {
  padding-top: 40px;

  .van-popup__close-icon {
    position: fixed;
    padding: 4px;
    font-size: 18px;
    color: #fff;
    background-color: @gray-light;
    border-radius: 50%;
  }
}

.agreement {
  padding: 0 12px 10px;
  font-size: 12px;

  .text-center {
    font-size: 16px;
  }

  h3 {
    margin: 18px 0 6px;
  }

  p {
    margin: 4px 0;
  }

  div {
    margin: 4px;
    font-size: 14px;
    text-indent: 1em;
  }

  ul {
    padding-left: 20px;
    margin-top: 8px;
  }

  li {
    font-size: 14px;
  }
}

.relation {
  width: 80%;
  padding: 20px 0 0;
  margin: 0 auto;

  .relation-title {
    position: relative;
    font-size: 9px;
    color: #9aacbd;
    text-align: center;

    &:before,
    &:after {
      position: absolute;
      top: 50%;
      width: 62px;
      height: 1px;
      background-color: #d8e2ec;
      content: '';
      transform: translateY(-50%);
    }

    &:before {
      left: 0;
    }

    &:after {
      right: 0;
    }
  }

  .relation-icon {
    padding: 10px 0 0;
    text-align: center;
  }

  .relation-icon-item {
    display: inline-block;
    width: 36px;
    height: 36px;

    &.relation-wechat {
      background: url(./../../imgs/wechat.png) no-repeat;
      background-size: contain;
    }
  }
}

.copyright {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 100%;
  padding: 8px 0;
  color: @gray-darker;
  text-align: center;
  transform: translateX(-50%);

  .copyright-item {
    font-size: 10px;
  }
}
