.load {
  z-index: 999;
  background: url('./../imgs/load.png') no-repeat bottom center;
  background-color: #fff;
  background-size: contain;
  opacity: 1;

  .load-logo {
    width: 100px;
    margin: -100px auto 0;
  }

  .load-title {
    margin-top: 10px;
    text-align: center;

    span {
      display: inline-block;
      margin: 0 12px;
      font-size: 20px;
    }
  }
}

#nprogress .bar {
  background: @blue !important; //自定义颜色
}

.uploader-sign {
  width: 100%;

  .van-uploader__input-wrapper {
    width: 100%;
  }

  .van-uploader__preview {
    margin: 0 auto;

    .van-image__img {
      border: 2px solid #fff;
      border-radius: 50%;
    }
  }
}

.btn-submit {
  padding: 20px;
}

.flex {
  display: flex;
}

.flex-item {
  flex: 1;
}

.flex-center {
  align-items: center;
}

.m-auto {
  margin: 0 auto;
}

.text-small {
  font-size: 12px;
  color: @gray;
}

body {
  background-color: #ececec;
}

* {
  font-size: 14px;
}

.van-nav-bar {
  .van-icon,
  .icon {
    font-size: 24px;
    color: #fff;
  }
}

.van-cell__left-icon {
  &.icon {
    font-size: 18px;
    color: @gray-darker;
  }
}

// .icon {
//   position: relative;
//   display: inline-block;
//   font-size: 14px/1;
//   text-rendering: auto;
