@page-top: 44px;

.main {
  height: ~'calc( 100% - 44px )';
  padding-top: @page-top;

  &.main-tabbar {
    height: ~'calc( 100% - 94px )';
    padding-bottom: 50px;
  }

  &.main-top,
  &.main-user {
    padding-top: 0;
  }

  .van-swipe-item {
    img {
      width: 100%;
    }
  }
}

.abs-full {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.page {
  &.page-signUp {
    .page-main {
      padding-bottom: 110px;
    }

    .page-foot {
      padding: 0;
    }

    .page-foot-text {
      padding: 8px 10px;
      border-bottom: 1px solid #ebedf0;

      .van-checkbox__label span {
        font-size: 12px;
      }
    }

    .page-foot-num {
      padding: 0 10px 8px;

      .fy {
        font-size: 30px;
      }

      span {
        font-size: 14px;
      }
    }
  }

  .abs-full();

  .page-main {
    padding-top: @page-top;
    padding-bottom: 60px;
    overflow: auto;
    .abs-full();
  }

  .page-foot {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    padding: 5px 16px;
    background-color: #fff;
    box-sizing: border-box;

    .van-button--block {
      height: 40px;
      line-height: 38px;
    }
  }
}

.logo {
  text-align: center;

  img {
    width: 100px;
  }
}

.tab-icon {
  .van-tab__text .icon {
    display: block;
    height: 20px;
    font-size: 18px;
    line-height: 26px;
    text-align: center;
  }
}

.van-tabs__line {
  background-color: @blue;
}

.van-tab--active {
  color: @blue;
}

.nav {
  z-index: 10;
  color: #fff;
  background: linear-gradient(54deg, rgba(255, 128, 8, 1) 0, rgba(255, 200, 55, 1) 100%);

  .van-nav-bar__title {
    color: #fff;
  }
}

.nav-transparent {
  background: transparent;

  &.van-hairline--bottom:after {
    display: none;
    content: '';
  }

  .van-nav-bar__title {
    max-width: 100%;
    padding-top: 4px;
    padding-left: 14px;
    font-size: 22px;
    text-align: left;
    text-shadow: 1px 1px 6px rgba(51, 51, 51, 0.6);
  }

  & + .ranking-bg {
    margin-top: -48px;
  }

  & + .home-swiper {
    margin-top: -40px;
  }
}

.nav-white {
  background: #f2f3f5;

  &.van-hairline--bottom:after {
    display: none;
    content: '';
  }

  .van-nav-bar__title {
    max-width: 100%;
    padding-left: 14px;
    font-size: 20px;
    color: #333;
    text-align: left;
  }

  .van-nav-bar__right .van-icon {
    color: #333;
  }
}

.nav-tp {
  padding-top: 0 !important;

  .nav {
    background: transparent;

    &[class*='van-hairline']:after {
      display: none;
      content: '';
    }
  }
}

.nav-opa {
  background-color: transparent;

  &.van-hairline--bottom:after {
    border-color: transparent;
    content: '';
  }
}

.van-button--prohibit {
  color: #fff;
  background-color: #8e8e8e;
  border: 1px solid #8e8e8e;
}

.btn-round--small {
  border-radius: 4px;
}

.cell-col {
  position: relative;
  padding: 8px;
  background: #fff;

  &:not(:last-child):before {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    pointer-events: none;
    border-bottom: 0.02667rem solid #ebedf0;
    content: ' ';
    transform: scaleY(0.5);
    box-sizing: border-box;
  }
}

.cell-list {
  padding: 12px;

  .van-panel__header {
    display: none;
  }

  .van-image {
    width: 100px;
  }

  // // .title,
  // .content {
  //   color: @gray;
  // }

  .title {
    margin-bottom: 4px;
    font-size: 16px;
    .ellipsis();
  }

  .content {
    margin-top: 8px;
    font-size: 10px;
    color: @gray;
    .ellipsis();

    .icon {
      font-size: 12px;
    }
  }

  .btn-container {
    text-align: right;
  }

  .tag-container {
    // margin-top: 4px;
    // text-align: right;
    .van-tag {
      margin-top: 18px;
    }
  }
}

.card {
  background: #fff;
  border-radius: 4px;
  // box-shadow: @box-show;
}

.card-text {
  padding: 14px;
  // margin: 8px;
}

.cell-list-home {
  .van-image {
    width: 100px;
    margin: 0 auto;
  }
}

.cell-list-match {
  .tag-container {
    .text-primary {
      font-size: 24px;
      line-height: 20px;
    }

    .text-small {
      font-size: 10px;
    }
  }
}

.match-tab .van-tabs__content {
  margin-top: 14px;
}

.match-rele {
  padding: 0 20px;

  .match-rele-bg {
    height: 476px;
    padding: 120px 24px 0 40px;
    background: url(./../imgs/rule-bg.png) no-repeat;
    background-size: cover;
  }

  .match-rele-content {
    height: 264px;
    overflow: auto;
  }
}

@matchTop: 48px;

.match-status {
  position: fixed;
  top: 39px;
  z-index: 10;
  width: 100%;
  height: @matchTop;
  background-color: #ffebcd;

  .match-status-left {
    float: left;
    width: 120px;
    height: ~'calc( 100% - 4px )';
    padding: 3px 0 0 4px;
    margin: 1px 0;
    font-size: 12px;
    color: #fff;
    background-image: linear-gradient(141deg, #fa5146, #f8a370 100%);
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
  }

  .match-status-left-label {
    font-size: 16px;
  }

  .match-status-right {
    position: absolute;
    right: 0;
    // float: right;
    padding-right: 10px;
    margin-top: 10px;
    font-size: 14px;

    .van-count-down {
      display: inline-block;

      .text-danger {
        margin: 0 4px;
      }
    }
  }
}

.match-active {
  &.van-tabs {
    padding-top: 40px;
  }
}

.match-banner {
  position: relative;

  .match-banner-main {
    position: absolute;
    top: 40%;
    z-index: 1;
    width: 100%;
    color: #fff;
    text-align: center;

    .title {
      margin-bottom: 8px;
      font-size: 18px;
      .fw-bold();
    }
  }

  .match-banner-copy {
    .item {
      padding: 6px 0;

      .van-button {
        margin-left: 8px;
      }
    }
  }
}

// 排行
.dialog-rank {
  height: 100%;
  overflow: hidden;

  .rank-list {
    height: 100%;
    overflow: hidden;
  }

  .dialog-rank-main {
    height: 100%;
    overflow: auto;
  }
}

.rank-main {
  padding: 10px 14px;
  background-color: #fff;
}

.rank {
  text-align: center;

  &.rank-col {
    .rank-icon {
      top: 0;
      width: 22px;
      height: 40px;
    }
  }

  .rank-sign {
    position: relative;
    padding-bottom: 34px;
  }

  .user-head-sign {
    width: 70px;
    height: 70px;
  }

  .rank-icon {
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 36px;
    height: 56px;
    background-repeat: no-repeat;
    background-size: contain;
    transform: translateX(-50%);
  }

  &.rank-one {
    .rank-icon {
      // background-position: 0 0;
      background-image: url(./../imgs/record-1.png);
    }
  }

  &.rank-two {
    .rank-icon {
      // background-position: 0 0;
      background-image: url(./../imgs/record-2.png);
    }
  }

  &.rank-three {
    .rank-icon {
      background-image: url(./../imgs/record-3.png);
      // background-position: 0 0;
    }
  }
}

.rank-list {
  .user-head-sign {
    width: 30px;
    height: 30px;
  }

  .reward-icon {
    position: absolute;
    top: 50%;
    left: 50px;
    transform: translateY(-50%);
  }
}

.rank-list-name {
  .ellipsis();
}

.rank-own {
  .rank-own-icon {
    width: 80px;
    height: 30px;
    font-size: 18px;
    font-weight: bold;
    line-height: 32px;
    color: #fff;
    background-image: url(./../imgs/rank-own.png);
    background-size: 100% 100%;
  }

  .user-head-sign {
    width: 40px;
    height: 40px;
  }
}

.list-head .cell-col {
  font-size: 12px;
  background-color: #fbf8f2;
}

.reward-icon {
  display: inline-block;
  width: 14px;
  height: 20px;
  vertical-align: middle;
  background-repeat: no-repeat;
  background-size: contain;

  &.reward-one {
    background-image: url(./../imgs/record-1.png);
  }

  &.reward-two {
    background-image: url(./../imgs/record-2.png);
  }

  &.reward-three {
    background-image: url(./../imgs/record-3.png);
  }

  &.rank-no {
    background-image: none;
  }
}

// 消息
.icon-colors {
  display: inline-block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  color: #fff;
  text-align: center;
  border-radius: 50%;

  &.icon-colors-primary {
    background-image: linear-gradient(to bottom right, #fece4d, #fa7893);
    box-shadow: 0 0 6px 0 #fa7893;
  }

  &.icon-colors-blue {
    background-image: linear-gradient(to bottom right, #89f3fd, #68abff);
    box-shadow: 0 0 6px 0 #68abff;
  }

  &.icon-colors-violet {
    background-image: linear-gradient(to bottom right, #b991fe, #8c73fa);
    box-shadow: 0 0 6px 0 #8c73fa;
  }

  &.icon-colors-pink {
    background-image: linear-gradient(to bottom right, #fd7fb3, #fc6288);
    box-shadow: 0 0 6px 0 #fc6288;
  }
}

// panel

.panel-default {
  .van-panel__header-value {
    color: @gray;
  }

  .van-panel__footer {
    text-align: right;
  }
}

.panel-head {
  padding: 8px 12px;

  .title {
    font-size: 16px;
    .fw-bold();
  }
}

.panel-main {
  padding: 10px 20px;
}

.search {
  position: absolute;
  top: 44px;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;

  .search-head {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
  }

  .search-main {
    height: 100%;
    padding-top: 45px;
    overflow: auto;
  }
}

.wallet {
  .wallet-item {
    border: 2px solid transparent;

    &.active {
      border: 2px solid @blue;
    }

    .label {
      font-size: 18px;
      color: @blue;
    }

    .small {
      margin-top: 4px;
      font-size: 14px;
    }
  }
}

.paytype {
  .paytype-icon {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-right: 6px;
    vertical-align: middle;
    background-repeat: no-repeat;
    background-size: cover;

    &.paytype-icon-wechat {
      background-image: url(./../imgs/icon-pay-wechat.png);
    }

    &.paytype-icon-zfb {
      background-image: url(./../imgs/icon-pay-zfb.png);
    }
  }
}

.count {
  padding: 20px 0;
  color: #fff;
  text-align: center;
  background-color: @blue;

  .label {
    margin-bottom: 10px;
    font-size: 14px;
  }

  .value {
    font-size: 24px;
    letter-spacing: 2px;
  }
}

.signUp-rule {
  position: absolute;
  top: 8px;
  right: 10px;
}

.signup-list-main {
  .van-grid-item {
    width: 100% / 6;
  }

  .van-grid-item__content--center {
    padding-right: 2px;
    padding-left: 2px;
  }

  .signup-list {
    width: 100%;
  }
}

.signup-list {
  position: relative;
  text-align: center;

  .user-head-sign {
    width: 40px;
    height: 40px;
  }

  .signup-list-label {
    .ellipsis();
  }

  .is-challenge {
    position: absolute;
    right: 10px;
    bottom: 24px;
  }

  // .van-image--round {
  //   overflow: initial !important;
  // }
}

.video-js .vjs-big-play-button {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.van-swipe {
  .video-js {
    width: 375px;
    height: 210px;
  }
}

// 排行榜 s
.ranking-bg {
  position: relative;
  padding: 100px 18px 18px;
  background: url(./../imgs/ranking-bg.png) no-repeat center;
  background-size: cover;

  &:before {
    position: absolute;
    top: 0;
    right: 40px;
    width: 150px;
    height: 150px;
    background: url(./../imgs/ranking-icon.png) no-repeat center;
    background-size: cover;
    content: '';
  }
}

.ranking {
  position: relative;
  height: 180px;
  padding-top: 10px;
  // color: #fff;
  text-align: center;
  background-color: #fff;
  // background: url(./../imgs/match-banner.png) no-repeat center;
  // background-size: 100% 100%;
}

.ranking-toggle {
  height: 100%;
  padding-top: 4px;
  font-size: 16px;
  line-height: 18px;
  // position: absolute;
  // top: 0;
  // right: 0;
  // padding: 8px;

  .icon {
    font-size: 16px;
  }
}

.ranking-main {
  .ranking-type {
    position: absolute;
    left: 2px;
  }

  .ranking-icon {
    position: absolute;
    top: 0;
    right: 0;
    padding: 8px;
    font-size: 20px;

    .icon {
      font-size: 20px;
      font-weight: bold;
    }
  }

  .label {
    font-size: 30px;
  }

  .label-bottom {
    margin-top: 10px;
    font-size: 16px;
  }

  .value {
    font-size: 40px;

    span {
      display: inline-block;
      font-size: 22px;
    }
  }

  .ranking-quarter {
    position: relative;
    height: 70px;
    margin-top: 14px;
  }

  .date-container {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }

  .ranking-countdown,
  .date {
    display: inline-block;
  }

  .ranking-countdown {
    width: 80px;
    // padding: 0 0 0;
    margin: 20px 0 0;
    line-height: 18px;
    color: @blue;
    text-align: center;
    border-bottom: 1px dashed #333;

    .day span {
      font-size: 32px;
      font-weight: bold;
    }
  }
}

.ranking-quarter {
  .label {
    font-size: 16px;
    .text-primary();
  }

  .date {
    font-size: 18px;
  }
}

.ranking-countdown {
  margin-top: 10px;
  font-size: 16px;
}
// 排行榜 e

// 支付 s
.pay-confirm {
  .van-dialog__footer--buttons {
    display: block;
  }

  .van-dialog__cancel {
    color: @red;
  }

  .van-dialog__confirm {
    color: @gray-darker;
  }
}
