@import "./../base/variables/vars";

.van-tabbar {
  z-index: 10;

  .van-tabbar-item {
    .icon {
      font-size: 20px;
    }
  }
}

.van-nav-bar__text {
  color: #fff;
}

// 地址管理
.van-address-list,
.van-address-edit__buttons {
  .van-button--danger {
    background-color: @blue;
    border-color: @blue;
  }
}

.van-address-edit {
  padding: 0;

  .van-address-edit__fields,
  .van-address-edit__default {
    border-radius: 0;
  }

  .van-address-edit__buttons {
    padding: 20px;
  }
}

// 开关
@switch: 28px;

.van-switch {
  width: @switch * 2;
  height: @switch;
}

.van-switch__node {
  width: @switch;
  height: @switch;
}

.van-switch--on .van-switch__node {
  transform: translateX(@switch);
}

// nav
.van-nav-bar__left,
.van-nav-bar__right {
  top: 0;
}

// panel
.panel-content {
  padding: 20px;
}

.van-empty {
  .van-empty__image {
    width: 100px;
    height: 100px;
  }

  .van-empty__description {
    font-size: 18px;
  }
}

.dialog-full {
  height: 90%;
  margin-top: 5%;

  .van-dialog__content {
    height: ~'calc( 100% - 50px )';
    overflow-x: hidden;
    overflow-y: auto;
  }
}

.dialog-main {
  padding: 10px 14px;
}

.van-tag--primary {
  background-color: @blue;
}

.van-tag--success {
  background-color: #07c160;
}
